.progressbar {
    height: 1rem;
    margin: 1rem 0;
    border-radius: .5rem;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(10%, #eeeeee), color-stop(40%, #ffffff));
    background: -webkit-linear-gradient(bottom, #eeeeee 10%, #ffffff 40%);
    background: -o-linear-gradient(bottom, #eeeeee 10%, #ffffff 40%);
    background: linear-gradient(to top, #eeeeee 10%, #ffffff 40%);
    border: 1px solid #e0e0e0;
    position: relative;
    overflow: hidden;
}

.progressbar__inner {
    height: 100%;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(10%, #015a8d), color-stop(40%, #0079bf));
    background: -webkit-linear-gradient(bottom, #015a8d 10%, #0079bf 40%);
    background: -o-linear-gradient(bottom, #015a8d 10%, #0079bf 40%);
    background: linear-gradient(to top, #015a8d 10%, #0079bf 40%);
}


